class Calculator extends React.Component {

    state = {
        res: "0"
    }
    alert = () => alert("My alert")

    render() {
        const alert2 = ()=> alert('Hi from alert2');

        return (
            <div>
                <div className="z-deph-2 light-blue lighten-2teal white-text center-align results">{this.state.res}</div>
                <div style={{ textAlign: "center" }}>
                    <a className="waves-effect waves-light btn" onClick={this.alert}>0</a>
                    <a className="waves-effect waves-light btn" onClick={alert2}>1</a>
                    <a className="waves-effect waves-light btn">2</a>
                </div>
                <style jsx>{
                    `
    .results{
            width: 300px;
            margin: 0 auto;
            font-size: 32px;
            margin-bottom: 10px;
        }
                `
                }</style>
            </div>
        )
    }
}

export default Calculator;